<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p>
      输入你要营销的关键字
    </p>
    名词：
    <input type="text" v-model="keyword1"/>&nbsp;&nbsp;&nbsp;&nbsp;
    形容词：
    <input type="text" v-model="keyword2"/>&nbsp;&nbsp;&nbsp;&nbsp;
    <button @click="generate">生成你的沙雕营销号文章</button>
    <h1>生成的文章</h1>
    <textarea  v-model="result" style="width:50%;height:200px;"/>
    <h3>我的博客</h3>
    <ul>
      <li><a href="https://cloudhao.top" target="_blank" rel="noopener">陈昀昊的博客</a></li>
    </ul>
      <span id="busuanzi_container_site_pv">本站总访问量<span id="busuanzi_value_site_pv"></span>次</span>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      keyword1:'营销号小编',
      keyword2:'弱智',
      result:''
    }
  },
  methods: {
    generate(){
      let content = "hello 大家好，这里是小x看世界。营销号小编弱智是怎么回事呢?营销号小编相信大家都很熟悉，但是营销号小编弱智是怎么回事呢?下面就让小编带大家一起了解吧。营销号小编弱智，其实就是弱智的营销号小编,大家可能会感到很惊讶，弱智的营销号小编怎么会这么弱智?但事实就是这样，小编也感到非常惊讶。那么这就是关于营销号小编弱智的事情了，大家有什么想法呢?欢迎在评论区告诉小编一起讨论哦。视频资料来自互联网，这里是小x看世界，我们下期再见，拜拜。"
      this.result = content.replace(/营销号小编/g,this.keyword1)
      this.result = this.result.replace(/弱智/g,this.keyword2)
    }
  },
}
</script>

